import mongoose from 'mongoose';
mongoose.Promise = global.Promise;

const Schema = mongoose.Schema;

const UserSchema = mongoose.Schema({
	accountType:{
		type: String,
		default: 'user'
	},
	superAgent: {
		type: String,
		default: 'diamond',
		required: true
	},
	agent: {
		type: String,
		default: 'diamond',
		required: true
	},
	username: {
		type: String,
		required: true
	},
	password: {
		type: String,
		default: '1234',
		required: true
	},
	passcode: {
		type: String,
		default: '4321',
		required: true
	},
	email: {
		type: String
	},
	phoneNumber: {
		type: String
	},
	minRiskAmount: {
		type: Number,
		default: 50,
		required: true
	},
	maxWagerAmount: {
		type: Number,
		default: 500,
		required: true
	},
	maxWinAmount: {
		type: Number,
		default: 5000,
		required: true
	},
	weeklyStartBalance: {
		type: Number,
		default: 5000,
		required: true
	},
	maxParlayTeam: {
		type: Number,
		default: 6
	},
	accountActive: {
		type: Boolean,
		default: true,
		required: true
	},
    createdAt: {
        type: Date
    }
});

const User = mongoose.model('user', UserSchema);

export default User;